.team {
	background: #fbfbfb;
	padding: 137px 0 177px;

	.container:nth-child(2) {
		margin-top: 124px; }

	&__wrapper {
		overflow: visible;
		position: relative;

		img {
			max-width: 100%; }

		&:hover {
			box-shadow: 0 0 49px 0 rgba(20, 20, 21, .11);
			overflow: hidden;

			.team__card {
				background: #fff3f0;
				box-shadow: -10px -34px 20px #fff3f0, 35px -32px 37px #fff3f0;
				display: block;
				height: 100%;
				top: 45%;
				width: 100%;
				@include pos-left;

				progress {
					background: #817f7e;
					border-radius: 1.5px;
					display: block;
					height: 4px;
					margin-top: 3px;
					width: 151px;

					&::-webkit-progress-value {
						background: $main-color; } }

				.team__stack {
					@include flex-list;
					align-items: center;
					text-align: center;

					.team__skill {
						font: 400 11px 'Open Sans', sans-serif;
						margin: 4px 0; } } }

			.team__social {
				@include flex-list;
				@include pos-left;
				bottom: 47.5%;
				flex-direction: row;

				.team__contact {
					background: no-repeat center center;
					cursor: pointer;
					margin: 0 7px;
					height: 17px;
					width: 14px;

					&:nth-child(1) {
						background-image: url("../../icons/team/twitter.png"); }

					&:nth-child(2) {
						background-image: url("../../icons/team/facebook.png"); }

					&:nth-child(3) {
						background-image: url("../../icons/team/twitter.png"); } } }

			.team__human {
				background: transparent;
				bottom: 30px;
				box-shadow: none;
				margin: 0 auto;

				.team__name {
					margin-bottom: -1px; }

				.team__special {
					color: rgba(255, 114, 76, .9);
					font-size: 16.5px; } } } }

	&__card {
		display: none; }

	&__human {
		background: #fff;
		border-radius: 2px;
		bottom: -23px;
		box-shadow: 1px 5.9px 16px 0 rgba(20, 20, 21, .08);
		padding: 16px 0;
		text-align: center;
		width: 67%;
		@include pos-left; }

	&__name {
		color: rgba(0, 0, 0, .75);
		font: 600 30px 'Open Sans', sans-serif;
		margin-bottom: 4px; }

	&__special {
		color: rgba(0, 0, 0, 0.7);
		font: 300 15.5px 'Open Sans', sans-serif; } }







