@media (min-width: 1201px) {
	.promo {
		&__menu {
			cursor: text;
			height: auto;
			width: auto; } } }

@media (max-width: 1200px) {
	.promo {
		background-position: 100% 100%;

		&__line {
			display: block; }

		&__list {
			display: none; } }

	.btn_services {
		width: 55%; }

	.footer {
		&__copy {
			text-align: center; } } }

@media (max-width: 992px) {
	.promo {
		background: #f5f5f5;

		&__header {
			margin-bottom: 95px; } }

	.process, .works, .services, .team, .clients {
		padding: 90px 0 100px;

		.container {
			&:nth-child(2) {
				margin-top: 60px; } } }

	.btn_services {
		width: 100%; }

	.clients {
		&__item {
			padding: 65px 30px 20px; } }

	.footer {
		&__copy {
			margin: 22px auto 0; } } }

@media (max-width: 768px) {
	.title {
		font-size: 37px; }

	.btn {
		&_send {
			&:hover {
				background: $secondary-color url(../../icons/promo/arrow.png) no-repeat 87% 55%;
				background-blend-mode: normal;
				border-color: rgba(1, 2, 2, .07);
				color: #fff;
				font-weight: 400; } }

		&_process {
			&:hover {
				background: #fff; } }

		&_services {
			&:hover {
				color: rgba(0, 0, 0, .8);

				.line {
					background: #000; } } }

		&_footer {
			&:hover {
				background: transparent;
				color: rgba(0, 0, 0, 0.8); } } }


	.promo {
		padding-top: 60px;

		&__menu {
			cursor: auto;
			margin-left: auto; }

		&__form {
			margin-top: 75px; } }

	.process {
		&__wrapper {
			&:hover {
				box-shadow: none; } } }

	.services {
		&__item {
			&:hover(not:active) {
				box-shadow: none; } }

		&__img {
			display: block;
			margin: 50px auto 0; } }

	.team {
		&__wrapper {
			&:hover {
				box-shadow: none;
				overflow: visible;

				.team__card {
					display: none; }

				.team__human {
					background: #fff;
					bottom: -23px;
					box-shadow: 1px 5.9px 16px 0 rgba(20, 20, 21, .08);

					.team__name {
						margin-bottom: 4px; }

					.team__special {
						color: rgba(0, 0, 0, 0.7);
						font-size: 15.5px; } } } }

		&__employee {
			display: block;
			margin: 0 auto; } }

	.clients {
		&__arrow {
			display: none; }

		&__item {
			margin: 100px 0 15px;

			&:hover {
				border: 1px solid rgb(211, 211, 211);
				box-shadow: 0 0 1px 0 rgba(20, 20, 21, .75); } }

		&__list {
			margin: 20px 0 0 0; }

		&__rect {
			&:hover {
				background: #e0e0e0; } } }

	.partners {
		padding: 40px 0;

		&__img {
			display: block;
			margin: 0 auto;

			&:hover {
				filter: none; } } }

	.footer {
		padding: 50px 0;

		&__item {
			&:hover {
				background: #d3d3d3 no-repeat center center;
				box-shadow: none; } } } }

@media (max-width: 576px) {
	.services {
		&__steps {
			flex-direction: row;
			justify-content: center;
			margin: 0 0 20px 0; }

		&__item {
			background-position: 75% center; } } }
