.promo {
	background: #f5f5f5 url(../../img/promo/promo-bg.jpg) no-repeat 64% 100%/600px;
	padding-top: 92px;

	&__header {
		margin-bottom: 135px; }

	&__logo {
		border-color: $third-color;
		border: 3.5px solid rgba(23, 15, 28, .8);
		border-radius: 2px;
		color: #000;
		opacity: 0.7;
		font: 600 38px 'Open Sans', sans-serif;
		height: 55px;
		text-align: center;
		width: 149px;

		&::first-letter {
			color: rgba(252, 103, 68, 0.7); } }

	&__menu {
		cursor: pointer;
		height: 20px;
		position: relative;
		width: 34px; }

	&__line {
		background: $third-color;
		border-radius: 1.5px;
		display: none;
		height: 4px;
		left: 0;
		position: absolute;
		opacity: 0.6;
		width: 100%;

		&:nth-child(1) {
			top: 0; }

		&:nth-child(2) {
			top: 40%; }

		&:nth-child(3) {
			bottom: 0; } }

	&__list {
		@include flex-list;
		flex-direction: row;
		justify-content: space-between; }

	&__link {
		color: rgba(0, 0, 0, .75);
		font: 400 20px 'Open Sans', sans-serif;
		transition: .5s;

		&:hover {
			color: $main-color;
			text-decoration: none; } }

	&__form {
		background: #fff;
		border-radius: 2px;
		box-shadow: 0 0 57px 0 rgba(20, 20, 21, .2);
		margin-top: 107px;
		padding: 18px 35px 20px;

		input, textarea {
			border: none;
			border-bottom: 1px solid rgba(0, 0, 0, .22);
			display: block;
			margin-top: 17px;
			outline: none;
			padding: 10px 0px 3px;
			width: 100%;

			&:focus {
				box-shadow: 0 4px 7px rgba(0, 0, 0, 0.22); } }

		input::placeholder, textarea {
			color: rgba(0, 0, 0, .6);
			font: 400 15px 'Open Sans', sans-serif;
			text-transform: capitalize;
			-webkit-text-stroke-width: .3px; }

		textarea {
			padding: 10px 0 40px;
			resize: none; } }

	&__name {
		@include text;
		color: rgba(33, 26, 24, 0.9);
		font: 20px 'Open Sans', sans-serif;
		opacity: 0.9;
		-webkit-text-stroke-width: .5px;

		span {
			color: rgba(255, 92, 54, 0.9);
			font-weight: 600; } } }












