.process {
	padding: 138px 0 157px;

	.col-xl-3, .col-lg-4 {
		&:nth-child(1) .process__wrapper {
			background: #4eb2f7; }

		&:nth-child(2) .process__wrapper {
			background: #c049f8; }

		&:nth-child(3) .process__wrapper {
			background: #817eff; }

		&:nth-child(4) .process__wrapper {
			background: #f74770; } }

	.container:nth-child(2) {
		margin-top: 106px; }

	&__wrapper {
		padding: 53px 17px 35px;
		position: relative;
		transition: .3s;

		&:hover {
			box-shadow: 0 0 35px 0 rgba(20, 20, 21, 0.21); }

		.process__intro {
			background: no-repeat center top;
			color: rgba(255, 255, 255, 0.9);
			font: 600 20px 'Open Sans', sans-serif;
			letter-spacing: 0.2px;
			opacity: 0.9;
			padding-top: 64px;
			text-align: center; } }

	.col-xl-3, .col-lg-4 {
		&:nth-child(1) .process__intro {
			background-image: url("../../icons/process/tomi.png"); }

		&:nth-child(2) .process__intro {
			background-image: url("../../icons/process/set.png"); }

		&:nth-child(3) .process__intro {
			background-image: url("../../icons/process/setting.png"); }

		&:nth-child(4) .process__intro {
			background-image: url("../../icons/process/image.png"); } }

	&__text {
		color: rgba(255, 255, 255, .75);
		margin: 20px 0 32px;
		font: 400 15px/1.47 'Open Sans', sans-serif;
		letter-spacing: 0.15px;
		opacity: .75;
		text-align: center; } }


