.clients {
	padding: 134px 0 144px;

	.container {
		position: relative; }

	&__arrow {
		align-items: center;
		background: #fff;
		border-radius: 50%;
		box-shadow: 0.5px 3px 14px rgba(20, 20, 21, 0.08);
		cursor: pointer;
		display: flex;
		height: 48px;
		justify-content: center;
		position: absolute;
		top: 60%;
		transition: .5s;
		width: 48px;

		&__img {
			filter: none; }

		&:hover {
			background: $main-color;
			box-shadow: 0.5px 3px 14px 0 rgba(20, 20, 21, 0.28);

			.clients__img {
				filter: invert(1); } }

		&:nth-of-type(2) {
			left: -6px;
			z-index: 1; }

		&:nth-of-type(4) {
			right: -6px; } }

	&__item {
		border: 1px solid rgb(211, 211, 211);
		border-radius: 1px;
		box-shadow: 0 0 1px 0 rgba(20, 20, 21, .75);
		font: 400 17px/1.59 'Source Sans Pro', sans-serif;
		margin: 137px 0 54px;
		padding: 90px 50px 34px;
		position: relative;
		text-align: center;

		&:hover {
			border: none;
			box-shadow: 0 0 27px 0 rgba(20, 20, 21, .09); } }

	&__avatar {
		@include pos-left;
		background: #fff;
		border: 1px solid #d2d2d2;
		border-radius: 50%;
		box-shadow: 0 0 1px 0 rgba(20, 20, 21, .75);
		height: 99px;
		top: -56px;
		width: 99px; }

	&__opinion {
		color: rgba(0, 0, 0, 0.7); }

	&__rating {
		@include flex-list;
		flex-direction: row;
		justify-content: center;
		margin: 15px auto; }

	&__star {
		background: url("../../icons/clients/star.png") no-repeat center center;
		cursor: pointer;
		height: 12px;
		margin: 0 1px;
		width: 11px; }

	&__author {
		color: rgba(0, 0, 0, 0.8);
		font-style: italic;

		span {
			font-weight: 600;
			font-style: normal; } }

	&__list {
		@include flex-list;
		flex-direction: row;
		justify-content: center; }

	&__rect {
		background: #e0e0e0;
		border-radius: 2.5px;
		cursor: pointer;
		height: 6px;
		margin: 0 4px;
		transition: .5s;
		width: 18px;

		&:hover, &_active {
			background: $main-color; } } }









