.btn {
	@include text;
	border-radius: 27px;
	border: 1px solid rgba(1, 2, 2, .07);
	box-shadow: 1.9px 10.8px 20px 0 rgba(20, 20, 21, 0.08);
	color: rgba(0, 0, 0, .8);
	cursor: pointer;
	display: block;
	font: 400 16px 'Open Sans', sans-serif;
	margin: 20px auto 0;
	transition: .5s;

	&_send {
		background: $secondary-color url(../../icons/promo/arrow.png) no-repeat 87% 55%;
		color: #fff;
		padding: 16px 66px 15px;

		&:hover {
			background-blend-mode: difference;
			background-color: #fff;
			border-color: $main-color;
			color: rgba(0, 0, 0, .55);
			font-weight: 600; } }

	&_process {
		background: #fff;
		bottom: -25px;
		box-shadow: 1px 5.9px 14px 0 rgba(20, 20, 21, .09);
		left: 0;
		padding: 15px 50px 15px 72px;
		position: absolute;
		right: 0;
		width: 85%;

		&:hover {
			background: #d3d3d3; } }

	&_services {
		margin: 0;
		padding: 15px 50px 15px 72px;
		position: relative;
		width: 45%;

		&:hover {
			@include btn-hover;

			.line {
				background: #fff;
				transition: .5s; } } }

	&_footer {
		margin: 0 auto 50px;
		padding: 19px 79px;

		&:hover {
			@include btn-hover; } } }





