.services {
	padding: 140px 0 157.5px;

	.container:nth-child(2) {
		margin-top: 122px; }

	&__steps {
		display: flex;
		flex-direction: column;
		list-style: none;
		margin: 0;
		padding: 0; }

	&__item {
		background: no-repeat center center;
		border-radius: 2px;
		cursor: pointer;
		height: 92px;
		margin: 7.5px 0;
		width: 104px;

		&_active, &:hover {
			box-shadow: 0 0 36px 0 rgba(20, 20, 21, .2); }

		&:nth-child(1) {
			background-image: url("../../icons/services/diagram.png"); }

		&:nth-child(2) {
			background-image: url("../../icons/services/setting.png"); }

		&:nth-child(3) {
			background-image: url("../../icons/services/study.png"); }

		&:nth-child(4) {
			background-image: url("../../icons/services/diagram.png"); } }

	&__header {
		color: rgba(0, 0, 0, .85);
		font: 400 30px 'Open Sans', sans-serif;
		padding-bottom: 10px;
		position: relative;
		text-transform: capitalize;

		&:after {
			background: $secondary-color;
			bottom: 0;
			content: "";
			height: 3px;
			left: 0;
			position: absolute;
			width: 57px; } }

	&__description {
		color: rgba(0, 0, 0, 0.85);
		font: 400 16px/1.56 'Open Sans', sans-serif;
		margin-top: 13px; }

	&__img {
		max-width: 100%; } }


