@mixin text {
	text-align: center;
	text-transform: capitalize; }

@mixin pos-left {
	left: 50%;
	position: absolute;
	transform: translateX(-50%); }

@mixin flex-list {
	display: flex;
	flex-direction: column;
	list-style: none;
	margin: 0;
	padding: 0; }

@mixin btn-hover {
	background: $main-color;
	color: #fff;
	transition: .5s; }

