.footer {
	background: #f7f7f7;
	padding: 50px 0 81px;

	&__social {
		@include flex-list;
		flex-direction: row;
		justify-content: center; }

	&__item {
		background: #d3d3d3 no-repeat center center;
		border-radius: 50%;
		cursor: pointer;
		height: 52px;
		margin: 0 7.5px;
		transition: .5s;
		width: 54px;

		&:hover {
			background: $main-color;
			box-shadow: 0 0 23px 0 rgba(20, 20, 21, .29); } }

	&__link {
		align-items: center;
		color: #fff;
		display: flex;
		font-size: 21.5px;
		justify-content: center;
		margin-top: 15px;

		&:hover {
			color: #fff; } }

	&__copy {
		color: rgba(4, 4, 4, .6);
		font: 400 17px 'Open Sans', sans-serif;
		text-align: center; }

	#first {
		color: green; }

	#second {
		color: teal;
		list-style: none; } }






